.header {
  text-align: center;
  padding: 4% 0
}
.logoContainer {
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 50%;
  grid-template-columns: [first] 25% [second] 25% [third] 25% [fourth] 25% [end];
  width: 75%;
  margin: auto;
}
.VW, .TacoBell, .Target {
  margin: auto;
  height: 55%;
}
.TacoBell {
  grid-column: 2/span 2;
}
.ABinBev {
  grid-column: 1/span 2;
  width: 50%;
  margin: auto
}
.Adidas {
  grid-column: 3/span 2;
  width: 50%;
  margin: auto
}
/*===MEDIA QUERIES FROM https://css-tricks.com/snippets/css/media-queries-for-standard-devices/ =======*/
/* ================= TABLET ================*/
@media screen and (width: 741px){
  .logoRowOne {
    height: 40%;
  }
  .ATT {
    width: 110%
  }
  .ABinBev, .Adidas {
    width: 75%;
  }
}
/* ================= MOBILE ================*/
@media screen and (max-width: 740px){
  .header {
    font-size: 1.3rem
  }
  .logoContainer {
    grid-template-columns: [first] 50% [second] 50% [end];
    padding: 15% 0px;
  }
  .ABinBev {
    grid-column: 1;
    width: 100%;
  }
  .Adidas {
    grid-column: 2;
    width: 100%;
  }
}



