.container * {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;

}
.textContainer {
  width: 60%;
  padding: 0px 0px 0px 40px 
}
.text {
  font-size: 2rem;
  line-height: 3.6rem
}
/*===MEDIA QUERIES FROM https://css-tricks.com/snippets/css/media-queries-for-standard-devices/ =======*/
/* ================= TABLET ================*/
@media screen and (width: 741px){
  .textContainer {
    width: 100%
  }
  .text {
    font-size: 1.5rem
  }
}
/* ================= MOBILE ================*/
@media screen and (max-width: 740px){
  .textContainer {
    width: 85%;
    padding: 0;
    margin-left: auto;
  }
  .text {
    font-size: 1.2rem;
    line-height: 1.7rem;
  }
}



