.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}
.textContainer {
  width: 60%;
  padding: 0px 0px 0px 40px 
}
.text {
  font-size: 2rem;
  line-height: 3.6rem
}
.logoContainer {
  width: 25%;
}
/*===MEDIA QUERIES FROM https://css-tricks.com/snippets/css/media-queries-for-standard-devices/ =======*/
/* ================= TABLET ================*/
@media screen and (width: 741px){
  .text {
    font-size: 1.5rem
  }
}
/* ================= MOBILE ================*/
@media screen and (max-width: 740px){
  .container {
    -ms-flex-direction: column;
        flex-direction: column;
    height: unset;
  }
  .text {
    font-size: 1.2rem;
    line-height: 1.7rem;
  }
  .textContainer {
    width: 85%;
    padding: 0;
  }
  .logoContainer {
    width: unset
  }
}



