/* ========BACKGROUND AI========= */
:global(.showAI){
  background-image: url('../../assets/logos/AI.svg');
  background-repeat: no-repeat;
  background-position: center;
}
:global(.hideAI){
  background-repeat: no-repeat;
  background-position: center;
}
:global(.hideLineDesignWhite) {
  background-size: 300px 300px;
  background-repeat: no-repeat;
  background-position: 10px 20px
}
:global(.showLineDesignWhite) {
  background-image: url('../../assets/images/LineDesign_white.png');
  background-size: 300px 300px;
  background-repeat: no-repeat;
  background-position: 10px 20px
}
/* ========LANDING TEXT========= */
:global(.landingText){
  top: 35vh;
  left: 50%
}
/*===MEDIA QUERIES FROM https://css-tricks.com/snippets/css/media-queries-for-standard-devices/ =======*/
/* ================= TABLET ================*/
@media screen and (width: 741px){
  .textContainer {
    font-size: 2rem
  }
}
/* ================= MOBILE ================*/
@media screen and (max-width: 740px){
  .textContainer {
    font-size: 1.5rem
  }
}



