/* =========SAME AS MAKING.CSS =========*/
.flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 80%;
}
.lineDesign {
    height: 50%;
    width: 25%;
    background-image: url('../../assets/images/LineDesign_white.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.title {
    position: relative;
    top: 20px;
    left: 20px;
    font-size: 2rem;
    height: 300px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 300px;
}
.copy {
    font-size: 1.6rem;
    margin: 0 0 8% 0;
}
.descriptionContainer {
    height: 100vh;
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}
