
.Header {
  text-align: center;
  padding: 5% 0 4% 0;
}
.ProjectAligner {
  display: -ms-flexbox;
  display: flex;
  margin: auto;
  text-align: center;
  width: 80%;
  -ms-flex-align: center;
      align-items: center;
}
.ProjectContainer {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between
}
.Project {
  width: 23vw;
  margin: 5px
}
.ProjectImage {
  width: 100%
}
.ProjectTitle {
  font-size: 2.813rem;
  margin: 0
}
.ProjectText {
  font-size: 1.4rem
}
/*===MEDIA QUERIES FROM https://css-tricks.com/snippets/css/media-queries-for-standard-devices/ =======*/
/* ================= TABLET ================*/
@media screen and (width: 741px){
  .container {
    overflow-y: scroll
  }
  .ProjectContainer {
    height: 80%;
  }
  .Project {
    width: 30%
  }
  .ProjectTitle {
    font-size: 2rem
  }
  .ProjectText {
    font-size: 1rem
  }
}
/* ================= MOBILE ================*/
@media screen and (max-width: 740px){
  .container {
    overflow-y: scroll
  }
  .Header {
    font-size: 1.5rem
  }
  .ProjectAligner{
    height: unset;
    width: unset
  }
  .ProjectContainer {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 85%;
    margin: auto;
  }
  .Project{
    padding: 60px 0px;
    width: 100%
  }
  .ProjectText {
    font-size: 1rem
  }
  .Section {
    height: unset
  }
}



